"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { useMutation, useQuery } from "@tanstack/react-query";
import { FileText, Upload as UploadIcon, LogOut, Key, Search } from "lucide-react";
import { useAuthStore } from "@/lib/store/auth";
import { jobsApi } from "@/lib/api";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { FileUpload } from "@/components/upload/file-upload";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

export default function DashboardPage() {
  const router = useRouter();
  const { user, clearAuth } = useAuthStore();
  const isAuthenticated = useAuthStore((state) => state.token !== null && state.user !== null);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [customName, setCustomName] = useState("");
  const [uploadSuccess, setUploadSuccess] = useState<string | null>(null);

  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/login");
    }
  }, [isAuthenticated, router]);

  const uploadMutation = useMutation({
    mutationFn: jobsApi.upload,
    onSuccess: (data) => {
      setUploadSuccess(data.job_id);
      setSelectedFile(null);
      setCustomName("");
      // Redirect to job status page after 2 seconds
      setTimeout(() => {
        router.push(`/jobs/${data.job_id}`);
      }, 2000);
    },
  });

  const handleLogout = () => {
    clearAuth();
    router.push("/login");
  };

  const handleUpload = () => {
    if (!selectedFile) return;
    uploadMutation.mutate({
      file: selectedFile,
      name: customName || undefined,
    });
  };

  if (!user) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <div className="text-muted-foreground">Loading...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-background via-background to-muted">
      {/* Header */}
      <header className="border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <FileText className="h-6 w-6 text-primary" />
              <h1 className="text-2xl font-bold">Doc2MD</h1>
            </div>
            <div className="flex items-center space-x-4">
              <span className="text-sm text-muted-foreground">
                Welcome, <span className="font-medium text-foreground">{user.username}</span>
              </span>
              <Button
                variant="outline"
                size="sm"
                onClick={() => router.push("/api-keys")}
              >
                <Key className="h-4 w-4 mr-2" />
                API Keys
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={() => router.push("/jobs")}
              >
                <Search className="h-4 w-4 mr-2" />
                My Jobs
              </Button>
              <Button variant="ghost" size="sm" onClick={handleLogout}>
                <LogOut className="h-4 w-4 mr-2" />
                Logout
              </Button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="container mx-auto px-4 py-12">
        <div className="max-w-3xl mx-auto space-y-8">
          {/* Upload Card */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center space-x-2">
                <UploadIcon className="h-5 w-5" />
                <span>Upload Document</span>
              </CardTitle>
              <CardDescription>
                Upload your document to convert it to Markdown format
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              {uploadSuccess && (
                <div className="rounded-md bg-green-500/10 border border-green-500/20 p-4 text-sm text-green-600 dark:text-green-400">
                  <p className="font-medium">Upload successful!</p>
                  <p className="text-xs mt-1">Job ID: {uploadSuccess}</p>
                  <p className="text-xs mt-1">Redirecting to job status...</p>
                </div>
              )}

              {uploadMutation.isError && (
                <div className="rounded-md bg-destructive/10 border border-destructive/20 p-4 text-sm text-destructive">
                  Upload failed. Please try again.
                </div>
              )}

              <FileUpload
                onFileSelect={setSelectedFile}
                selectedFile={selectedFile}
                onClear={() => setSelectedFile(null)}
              />

              {selectedFile && (
                <div className="space-y-2">
                  <Label htmlFor="customName">
                    Custom Name (Optional)
                  </Label>
                  <Input
                    id="customName"
                    type="text"
                    placeholder="e.g., Monthly Report 2025"
                    value={customName}
                    onChange={(e) => setCustomName(e.target.value)}
                  />
                  <p className="text-xs text-muted-foreground">
                    If not provided, the filename will be used
                  </p>
                </div>
              )}

              <Button
                onClick={handleUpload}
                disabled={!selectedFile || uploadMutation.isPending}
                className="w-full"
                size="lg"
              >
                {uploadMutation.isPending ? "Uploading..." : "Convert to Markdown"}
              </Button>
            </CardContent>
          </Card>

          {/* Info Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Supported Formats</CardTitle>
              </CardHeader>
              <CardContent>
                <ul className="text-sm space-y-2 text-muted-foreground">
                  <li>• PDF Documents</li>
                  <li>• Microsoft Word (DOCX, DOC)</li>
                  <li>• HTML Files</li>
                  <li>• PowerPoint (PPTX)</li>
                  <li>• Excel (XLSX)</li>
                  <li>• Rich Text Format (RTF)</li>
                  <li>• OpenDocument Text (ODT)</li>
                </ul>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="text-lg">How It Works</CardTitle>
              </CardHeader>
              <CardContent>
                <ol className="text-sm space-y-2 text-muted-foreground">
                  <li>1. Upload your document</li>
                  <li>2. Processing starts automatically</li>
                  <li>3. Track progress in real-time</li>
                  <li>4. Download Markdown output</li>
                  <li>5. Search and manage your jobs</li>
                </ol>
              </CardContent>
            </Card>
          </div>
        </div>
      </main>
    </div>
  );
}
